.about {
    background-color: $dark;
    &__inner {
        padding: 50px 0px;
    }
    &__title {
        max-width: 335px;
        margin-bottom: 40px;
    }
    &__info {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    &-block {
        width: 100%;
        max-width: 540px;
        margin-bottom: 30px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &-box {
        &__title {
            @include f600;
            font-size: 28px;
            line-height: 42px;
            margin-bottom: 5px;
            color: #fff;
        }
    }
    &-text {
        @include f300;
        color: #fff;
        line-height: 30px;
    }
}