.footer {
    background-color: $dark;
    &__inner {
        padding: 70px 0px 30px 0px;
        display: flex;
        justify-content: space-between;
        color: #fff;
    }
    &-social {
        max-width: 210px;
        width: 100%;
        margin-right: 160px;
    }
    &-logo {
        @include f800;
        font-size: 24px;
        line-height: 36px;
    }
    &-text {
        font-size: 14px;
        line-height: 26px;
    }
    &-company {
        max-width: 100px;
        width: 100%;
        margin-right: 110px;
    }
    &-support {
        max-width: 205px;
        width: 100%;
        margin-right: 70px;
    }
    &-company,
    .footer-support {
        ul {
            padding: 0px;
            li {
                @include f300;
                line-height: 24px;
                &+li {
                    margin-top: 10px;
                }
            }
        }
    }
    &-title {
        @include f500;
        font-size: 18px;
        line-height: 27px;
        margin-bottom: 20px;
    }
    &-mail {
        max-width: 255px;
        width: 100%;
        position: relative;
        input {
            background-color: $dark-grey;
            border: none;
            width: 100%;
            padding: 10px 30px 10px 12px;
            border-radius: 8px;
            color: #fff;
            @include f300;
            font-size: 14px;
            outline: none;
            &::placeholder {
                color: #fff;
            }
        }
        button {
            position: absolute;
            right: 10px;
            top: 55px;
            background-color: $dark-grey;
            border: none;
            border-radius: 8px;
            cursor: pointer;
        }
    }
}