.circle-icon {
    width: 100%;
    max-width: 70px;
    height: 70px;
    background-color: $light-dark;
    border-radius: 50%;
    position: relative;
    content: '';
    margin-right: 30px;
    top: -20px;
    svg {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }
}