.founder {
    background-color: $dark;
    &__inner {
        padding: 50px 0px;
    }
    &-title {
        text-align: center;
        line-height: 75px;
        margin-bottom: 50px;
    }
    &-content {
        display: grid;
        grid-template-columns: auto auto;
        gap: 25px;
        grid-template-rows: auto auto;
    }
    &-person {
        max-width: 540px;
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        text-align: end;
        height: fit-content;
        &:nth-child(even) {
            flex-direction: row-reverse;
            text-align: left;
            .founder-photo {
                margin-left: 0;
                margin-right: 14px;
            }
        }
        &:nth-child(odd) {
            margin-top: -70px;
        }
        &:nth-child(1) {
            margin-top: 0;
        }
        &:nth-child(2) {
            margin-top: 70px;
        }
        &:nth-child(3) {
            .founder-info {
                padding-bottom: 80px;
            }
        }
        &:nth-child(4) {
            .founder-info {
                padding-bottom: 90px;
            }
        }
    }
    &-info {
        padding-bottom: 10px;
    }
    &-photo {
        min-width: 285px;
        height: 355px;
        background-size: cover;
        margin-left: 14px;
    }
    &-name {
        display: inline-block;
        @include f500;
        font-size: 18px;
        line-height: 27px;
        color: #fff;
    }
    &-skills {
        display: inline-block;
        @include f300;
        font-size: 14px;
        line-height: 26px;
        color: #fff;
    }
}