.header {
    background-color: $dark;
    padding-bottom: 100px;
    &__top {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 15px 0px 8px 0px;
    }
    &__menu {
        li {
            color: #fff;
            @include f300;
            font-size: 14px;
            line-height: 24px;
            &+li {
                margin-left: 40px;
            }
        }
    }
    &__bottom {
        margin-top: 50px;
        width: 100%;
        height: 650px;
        background-size: cover;
        background-repeat: no-repeat;
        padding-left: 363px;
        padding-top: 207px;
    }
    &__title {
        color: #fff;
        @include f800;
        font-size: 61px;
        line-height: 92px;
    }
    &__text {
        color: #fff;
        font-size: 20px;
        line-height: 38px;
        margin-bottom: 30px;
    }
}