.project {
    background-color: $dark;
    &__inner {
        padding: 50px 0px;
    }
    &-head {
        display: flex;
        justify-content: space-between;
        margin-bottom: 50px;
        align-items: flex-end;
    }
    &-title {
        max-width: 430px;
    }
    &-content {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    &-images {
        margin-bottom: 30px;
        img {
            border-radius: 10px;
            background-size: cover;
            background-position: center;
            background-repeat: no-repeat;
        }
    }
}