.services {
    background-color: $dark;
    &__inner {
        padding: 50px 0px;
    }
    &-title {
        text-align: center;
        max-width: 600px;
        line-height: 83px;
        margin: 0 auto;
        margin-bottom: 50px;
    }
    &-content {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    &-box {
        max-width: 350px;
        width: 100%;
        margin-bottom: 30px;
        background-color: $light-dark;
        border-radius: 10px;
        padding: 25px 22px 40px;
        &__title {
            @include f600;
            font-size: 24px;
            line-height: 36px;
            color: #fff;
            margin-bottom: 10px;
        }
    }
    &-icon {
        margin-bottom: 50px;
    }
    &-text {
        color: #fff;
        line-height: 28px;
        margin-bottom: 50px;
    }
}