*,
*::before,
*::after {
    box-sizing: border-box;
}

html {
    font-size: 16px;
}

html,
body {
    min-height: 100%;
    position: relative;
}

body {
    font-family: 'Poppins', sans-serif;
    font-size: 16px;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

li,
a {
    display: inline-block;
    text-decoration: none;
}

.container {
    max-width: 1140px;
    padding: 0px 15px;
    margin: 0 auto;
    width: 100%;
}