.grow {
    background-color: $light-dark;
    &__inner {
        padding: 50px 0px;
        text-align: center;
    }
    &-title {
        margin: 0 auto;
        max-width: 650px;
        width: 100%;
        margin-bottom: 35px;
    }
    &-info {
        margin-top: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &-status {
        max-width: 240px;
        width: 100%;
        color: #fff;
        border-right: 1px #fff solid;
        border-left: 1px #fff solid;
        &+& {
            margin-left: 30px;
        }
    }
    &-number {
        @include f700;
        font-size: 28px;
        line-height: 40px;
    }
    &-text {
        line-height: 26px;
    }
}