.review {
    background-color: $dark;
    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 50px 0;
    }
    &-aside {
        padding-right: 53px;
        min-width: 490px;
    }
    &-title {
        line-height: 80px;
        margin-bottom: 10px;
    }
    &-text {
        color: #fff;
        font-size: 18px;
        line-height: 35px;
    }
    &-slider {
        background-color: $light-dark;
        border-radius: 10px;
        padding: 37px 44px 56px 26px;
        position: relative;
        &__text {
            color: #fff;
            @include f300;
            line-height: 30px;
        }
    }
    &-brackets {
        position: absolute;
        right: 44px;
    }
    &-info {
        padding-top: 90px;
    }
    &-customer {
        display: flex;
        align-items: center;
        color: #fff;
        margin-bottom: 17px;
    }
    &-author {
        margin-right: 20px;
    }
    &-user {}
    &-name {
        @include f600;
        font-size: 18px;
        margin-bottom: 5px;
    }
    &-company {
        @include f300;
        font-size: 14px;
    }
}